	module.exports = {"data":{"post":{"html":"<p><img alt=\"Hamburg-Amerika Linie\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/02/hh-amerika.jpg\" width=\"189\" height=\"261\" border=\"0\" /></p>\n<p>Christopher's favorite picture right now is this poster of the steamship <a title=\"Ships of the World: An Historical Encyclopedia - - Deutschland\" href=\"http://college.hmco.com/history/readerscomp/ships/html/sh_026600_deutschland2.htm\">Deutschland</a>, which we have hanging in our hallway for obvious sentimental reasons. Every evening on his way to bed, he has to sit on the stairs for 2 or 3 minutes and admire the <em>big ship</em>, with four <em>smokestacks</em> and an <em>anchor</em>. </p>\n<p>The Hamburg-Amerika Linie is today called <a href=\"http://www.hapag-lloyd.com/\">Hapag-Lloyd</a>, and they still sail ships. And fly tourist jets (although they recently sold the airline to <a href=\"http://www.tui.com/en\">TUI</a>). Christopher would approve of both lines of business.</p>","fields":{"title":"Hamburg-Amerika Linie","date":"17 Feb 2003","url":"https://papascott.de/2003-02-17-hamburg-amerika-linie/"}}},"pathContext":{"slug":"/2003-02-17-hamburg-amerika-linie/","prev":{"fields":{"title":"Duct Tape for Weasels","date":"15 Feb 2003","slug":"/2003-02-15-duct-tape-for-weasels/"}},"next":{"fields":{"title":"Lost in the Translation","date":"19 Feb 2003","slug":"/2003-02-19-lost-in-the-translation/"}}}}

